export default {
	props: {
	},
	data() {
		return {
			isShowMask: false
		}
	},
	methods: {
		
		toAddfriend() {
			uni.navigateTo({
				url: '/components/content/addfriend/AddFriend'
			})
		},
		
		
		
		//显示遮罩层
		tapShowMask() {
			setTimeout(() => {
				this.isShowMask = true
			}, 200)
		},
		
		//隐藏遮罩层
		tapHideMask(e) {
			setTimeout(() => {
				this.isShowMask = false
			}, 200)
		},
		
		
		
		
	}
}
